
import { defineComponent, ref } from 'vue';
import { getAuth, signInWithEmailAndPassword, GoogleAuthProvider, signInWithPopup } from "firebase/auth";
import router from '@/router';
import { useRouter } from 'vue-router';
import MenuBar from '@/components/menuBar.vue';
import FooterBar from '@/components/FooterBar.vue';
import { insertUser } from '@/services/api';


export default defineComponent({


    name: 'login-view',
    setup() {
        const errMSG = ref("");
        const email = ref("");
        const password = ref("");
        const router = useRouter();
        const inputType = ref("password");
        let isValidEmail = true;
        return { email, validateEmail, isValidEmail, password, errMSG, router, inputType };

        function validateEmail() {
            return
        }
    },
    components: {

        MenuBar,
        FooterBar
    },

    methods: {
        toggleType() {
            this.inputType = this.inputType === "password" ? "text" : "password";
        },
        checkLogin() {

            console.log({
                email: this.email,
                password: this.password,
            })
        },
        register() {
            const auth = getAuth();
            signInWithEmailAndPassword(auth, this.email, this.password)
                // eslint-disable-next-line
                .then((data) => {
                    console.log(data);
                    alert("Conta logada!")
                    router.push(`/Painel/${data.user.uid}`)
                }).catch((error) => {
                    console.log(error.code);
                    switch (error.code) {
                        case "auth/invalid-email":
                            this.errMSG = "Email invalido!"
                            break;
                        case "auth/user-not-found":
                            this.errMSG = "Usuário sem registro encontrado!"
                            break;
                        case "auth/wrong-password":
                            this.errMSG = "Senha errada"
                            break;
                        default:
                            this.errMSG = "Email ou senha incorreta"
                            break;
                    }
                    // alert(error.message);
                })
        },
        signInWithGoogle() {
            const provider = new GoogleAuthProvider();
            // eslint-disable-next-line 
            signInWithPopup(getAuth(), provider).then((result) => {
                insertUser(
                    {
                        displayName: result.user.displayName,
                        email: result.user.email,
                        photoURL: "https://source.unsplash.com/random/151x151/",
                        sexo: "",
                        uid: result.user.uid,
                    }
                )
                router.push(`/Painel/${result.user.uid}`);
            }).catch((error) => {
